.home_page_container {
  @media only screen and (max-width: 700px) {
    padding: 1rem 1.5rem 0rem 1.5rem;
    // height: auto;
  }
  @media only screen and (min-width: 700px) {
    padding: 1rem 1.5rem 0rem 1.5rem;
  }
  .gif_container {
    @media only screen and (max-width: 700px) {
      display: flex;
      text-align: center;
      justify-content: center;
      z-index: 3;
      position: fixed;
      top: 4rem;
      position: relative;
    }
    @media only screen and (min-width: 700px) {
      display: flex;
      text-align: center;
      justify-content: center;
      z-index: 3;
      position: fixed;
      top: 4rem;
      position: relative;
    }
    img {
      @media only screen and (max-width: 700px) {
        border-radius: 30px;
        position: fixed;
        top: 2.5rem;
      }
      @media only screen and (min-width: 700px) {
        border-radius: 30px;
        position: fixed;
        top: 2.5rem;
      }
    }
  }
  .main_container {
    @media only screen and (max-width: 700px) {
    }
    .card_img {
      @media only screen and (max-width: 700px) {
        margin: auto;
        display: flex;
        top: 2rem;
        animation: mymove 3s forwards;
      }
      @media only screen and (min-width: 700px) {
        margin: auto;
        display: flex;
        top: 2rem;
        animation: mymove 3s forwards;
      }
    }
    @media only screen and (max-width: 700px) {
      @keyframes mymove {
        from {
          width: 0%;
        }
        to {
          width: 100%;
        }
      }
    }
    @media only screen and (min-width: 700px) {
      @keyframes mymove {
        from {
          width: 0%;
        }
        to {
          width: 25%;
        }
      }
    }
  }
}
.button {
  background-color: red !important;
  border-color: red;
  @media only screen and (max-width: 700px) {
    display: flex;
    justify-content: center;
    bottom: 30px;
  }
  @media only screen and (min-width: 700px) {
    display: flex;
    justify-content: center;
    bottom: 30px;
  }

  button {
    background-color: red !important;
    border-color: red;

    @media only screen and (max-width: 700px) {
      position: fixed !important;
      bottom: 1.5rem !important;
    }
    @media only screen and (min-width: 700px) {
      position: fixed !important;
      bottom: 1.5rem !important;
    }
  }
}
