//   FOR DESKTOP AND TABLET
.main_container {
  max-width: 100% !important;
  .background {
    background-color: #f0f2f7;
    height: 100vh;
  }
  .head {
    text-align: center;
    font-size: 2.1rem;
    font-weight: 600;
    color: #1b4192;
    margin-top: 1rem;
  }

  .container_left {
    background-color: #f0f2f7;
    padding: 0 !important;

    margin-top: 4rem;
    display: flex;
    place-content: center;
  }

  .cardPayment {
    border-radius: 0.5rem !important;
    width: 42%;
    margin: auto;
    margin-top: 0px;
    border: none;

    .logoImage {
      width: 150px;
      margin: 20px auto -20px auto;
    }
    .headingMain {
      color: #707070;
      font-size: 1.5rem;
      font-weight: bold;
      float: left !important;
    }
  }

  .buttonPayment {
    width: 100%;
    height: 3rem;
    background-color: #1b4192 !important ;
    color: white !important;
    border: none !important;
    border-radius: 0.5rem !important;
    padding-top: 0.3rem !important;
    padding-bottom: 0.3rem !important;
    font-size: 1rem !important;
    filter: drop-shadow(1px 5px 5px rgba(75, 56, 56, 0.192));
  }
  .label {
    color: #707070;
    font-size: 0.8rem;
    font-weight: bold;
    float: left;
  }
  .icon {
    margin-top: -1.7rem;
    margin-left: 0.6rem;
    float: left;
  }
  .inputBox1 {
    background-color: #ffffff;
    overflow: hidden;
    text-align: left;
    height: 55px;
    border: 1px solid #eeeeee;
    margin-bottom: 10px;
    border-radius: 10px;
    width: 100%;
    label {
      margin-left: 10px;
      font-family: ProximaNovaBold;
      font-size: 12px;
    }
    .input {
      margin-top: -10px;
      margin-left: 10px;
      width: 100%;
      input {
        background: transparent;
        border: none;
        outline: none;
        width: 100% !important;
      }
    }
  }
  .inputBox {
    background-color: #ffffff;
    overflow: hidden;
    color: #6b6f75;
    text-align: left;
    height: 55px;
    border: 1px solid #eeeeee;
    margin-bottom: 10px;
    border-radius: 10px;
    width: 100%;
    label {
      margin-left: 10px;
      font-family: ProximaNovaBold;
      font-size: 12px;
    }
    .input {
      margin-top: -10px;
      margin-left: 10px;
      width: 100%;
      input {
        background: transparent;
        border: none;
        width: 100%;
        outline: none;
      }
    }
  }

  .inputBoxwhite1 {
    background-color: #ffffff;
    overflow: hidden;
    text-align: left;
    height: 55px;
    border: 1px solid #eeeeee;
    margin-bottom: 10px;
    border-radius: 10px;
    width: 100%;
    label {
      margin-left: 10px;
      font-family: ProximaNovaBold;
      font-size: 12px;
    }
    .input {
      margin-top: -2px !important;
      margin-left: 10px;
      width: 100%;

      input {
        background: transparent;
        border: none;
        outline: none;
        width: 100%;
      }
    }
  }
  .inputBoxwhite {
    background-color: #eeeeee;
    overflow: hidden;
    color: #6b6f75;
    text-align: left;
    height: 55px;
    border: 1px solid #eeeeee;
    margin-bottom: 10px;
    border-radius: 10px;
    width: 100%;

    label {
      margin-left: 10px;
      font-family: ProximaNovaBold;
      font-size: 12px;
    }
    .input {
      margin-top: -2px !important;
      width: 95%;

      margin-left: 10px;
      input {
        background: transparent;
        border: none;
        width: 100%;

        outline: none;
      }
    }
  }
}
.menuButton {
  display: none;
}
