.mainContainer {
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 50px;
  position: fixed;
  top: 0;
  width: calc(100% - 250px);
  margin-left: 250px;
  z-index: 900;

  .profileContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: auto;
    .notificationsContainer {
      width: 20px;
      .icon {
        path {
          fill: #b8b5b5;
        }
      }
    }

    .dropdown {
      button {
        background: none !important;
        border: none !important;
        transition: none !important;
        &:focus {
          border: none !important;
          outline: none !important;
        }
      }
      .profile {
        margin-left: 20px;
        display: flex;
        align-items: center;
        .picContainer {
          width: 40px;
        }
        .headingContainer {
          margin: 0 0px 0 8px;
          h6 {
            margin: 0;
            color: #5a5a5a;
            font-size: 16px;
            font-family: ProximaNovaBold;
          }
        }
        .iconContainer {
          margin-left: 15px;
          width: 10px;
          .icon {
            path {
              fill: #1b4192;
            }
          }
        }
      }
    }
  }
}
