.home_page_container {
  @media only screen and (max-width: 700px) {
    background-color: rgba(255, 255, 255, 255) !important;
    text-align: center;
  }
  @media only screen and (min-width: 700px) {
    background-color: rgba(255, 255, 255, 255) !important;
    text-align: center;
  }
  .main_container {
    @media only screen and (max-width: 700px) {
      margin: auto;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    @media only screen and (min-width: 700px) {
      margin: auto;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .col_one {
      position: absolute;
      top: 0;
      margin: auto;
      padding-top: 3rem;
      text-align: center;
    }
    .col_two {
      position: absolute;
      bottom: 0;
      margin: auto;
      text-align: center;
      padding-bottom: 2rem;
    }
    .col_three {
      position: absolute;
      top: 0;
      margin: 6rem auto;
      text-align: center;
      @media only screen and (max-width: 700px) {
        margin: 7rem auto;
      }
      #red_btn {
        background-color: red !important;
        border: none;
        padding: 0.75rem 6.8rem !important;
        font-size: 18px !important;
        font-weight: 500 !important;
        @media only screen and (min-width: 700px) {
          background-color: red !important;
          border: none;
          padding: 0.4rem 4.55rem !important;
          font-size: 18px !important;
          font-weight: 500 !important;
        }
      }
    }
  }
}
