@media only screen and (min-width: 600px) {
  * {
    margin: 0rem;
    padding: 0rem;
    box-sizing: border-box;
  }
  .table_container {
    margin-top: 6.5rem !important;
    overflow: hidden !important;
    background-color: transparent;
    padding: 0.8rem;
    --bs-gutter-x: 0rem !important;
    // margin: 1rem;
    .main_container {
      margin-left: 18rem !important;
    }
    .row_bar {
      margin: 0rem;
      padding: 0rem;
      .button {
        display: flex;
        justify-content: flex-end;
        .sink_button {
          background-color: #1b4192 !important;
          color: white;
          width: auto;
          height: 2.5rem;
          border: none;
          margin-right: 1rem;
        }

        .refresh_button {
          background-color: #1b4192 !important;
          color: white;
          width: auto;
          height: 2.5rem;
          border: none;
        }
      }

      .dashboard_heading {
        margin-bottom: 0.5rem !important;
        font-weight: 800;
        color: #1b4192;
      }
    }
    .loader_styling {
      width: 100%;
      display: flex;
      justify-content: center;
    }
    .table_bg {
      background-color: white !important;
      border-radius: 0.1rem;

      .table_heading {
        text-align: center;
        background-color: #1b419217;
        th {
          text-transform: capitalize;
          text-align: left;
        }
      }

      .align_row {
        vertical-align: baseline;
        .card_img {
          width: 2rem;
        }
        .status_icon {
          width: 1rem;
        }
      }
    }
    .pagination {
      padding-top: 0.8rem;
      display: flex;
      justify-content: center;
    }
  }
}
